import ImageContent from "../../../../components/partials/blocks/left-image-right-content";
import React from "react";
import {Link} from "gatsby";
import styled from "styled-components";
import {Heading} from "../../../atoms/typography/heading";
import {Paragraph} from "../../../atoms/typography/paragraph";
import {color} from "../../../../components/styles/colors";
import TextBlockWithImage from "../../../molecules/mixed-blocks/text-block-with-absolute-image";
import ArrowSVG from "../../../../images/arrow.inline.svg";
import IconCircle from "../../../../components/icons/circle";
import PlaneIcon from "../../../../images/icons/plane.inline.svg";
import RockerIcon from "../../../../images/icons/rocket.inline.svg";
import SeriesIcon from "../../../../images/icons/limited-liability-company.inline.svg";
import HandsIcon from "../../../../images/icons/hands.inline.svg";
import ArrowsIcon from "../../../../images/icons/arrows.inline.svg"
import Button from "../../../molecules/buttons/button";
import OvalSvg from "../../../../images/ovals/top-left-transparent-orange2.inline.svg";
import {shadow} from "../../../atoms/styles/shadows";
import {gradient} from "../../../atoms/styles/colors";

const BusinessOwnerResponsibilities = styled.section`
  position: relative;
  padding-top: 75px;
  padding-bottom: 64px;

  .colorbox {
    margin-bottom: 48px;
  }
`;

const OrangeContainer = styled.div`
    background: rgb(255,255,255);
    background-image: ${gradient.orange3};
    padding-bottom: 100px;
    position: relative;
`;

const Oval = styled.div`
    position: absolute;
    left: 0;
    top: 100px;
    width: 100%;
    
    @media (min-width: 570px) {
        width: 570px;
    }
`

const BusinessStructure = () => <OrangeContainer>

    <Oval>
        <OvalSvg/>
    </Oval>

    <BusinessOwnerResponsibilities>
        <ImageContent image="unique-business-2" paddingTop="60">
            <Heading size={4} style={{fontSize: "24px"}}>
                Choose the Right Business Structure and Register Your Consulting Business
            </Heading>
            <Paragraph big>
                Now that you have all the background information for your consulting business, its time to make
                it into a reality. That starts by choosing the right structure or legal entity for your
                business. In the U.S., there are four main business structures. They are:
            </Paragraph>

            <TextBlockWithImage
                SvgImage={HandsIcon}
                textBackgroundColor="transparent"
                imageBackgroundColor={color.white}
                imageShadowColor={shadow.white2}
                imageShadowOpacity={0.5}
                boxShadow={false}
                paddingLeft={0}
                style={{marginTop: "40px"}}
                circleShadowY={40}
                circleShadowBlure={80}
                width={100}
                widthUnit="%"
            >
                <Paragraph big style={{color: color.blue1, fontWeight: "bold"}}
                           mixed={true}>
                    <FlexLink to="/">
                        <IconCircle circleColor="transparent" iconColor={color.blue1}>
                            <ArrowSVG/>
                        </IconCircle>
                        Sole Proprietorship
                    </FlexLink>
                </Paragraph>

                <Paragraph big>
                    This is the "default" business structure and is what your business will be if you decide not
                    to create a more formal structure. We don't recommend this type of business as it doesn't
                    give you the legal protections you need.
                </Paragraph>
            </TextBlockWithImage>

            <TextBlockWithImage
                SvgImage={ArrowsIcon}
                textBackgroundColor="transparent"
                imageBackgroundColor={color.white}
                imageShadowColor={shadow.white2}
                imageShadowOpacity={0.5}
                boxShadow={false}
                paddingLeft={0}
                style={{marginTop: "40px"}}
                circleShadowY={40}
                circleShadowBlure={80}
                width={100}
                widthUnit="%"
            >
                <Paragraph big flex flexAlign={true} style={{color: color.blue1, fontWeight: "bold"}}
                           mixed={true}>
                    <IconCircle circleColor="transparent" iconColor={color.blue1}>
                        <ArrowSVG/>
                    </IconCircle>
                    Limited Liability Company or LLC
                </Paragraph>

                <Paragraph big>
                    The most common type of business entity. An LLC is fast, simple and inexpensive to setup and
                    maintain. It protects your personal finances and assets and is a great way to start your
                    real estate
                </Paragraph>
            </TextBlockWithImage>

            <TextBlockWithImage
                SvgImage={SeriesIcon}
                textBackgroundColor="transparent"
                imageBackgroundColor={color.white}
                imageShadowColor={shadow.white2}
                imageShadowOpacity={0.5}
                boxShadow={false}
                paddingLeft={0}
                style={{marginTop: "40px"}}
                circleShadowY={40}
                circleShadowBlure={80}
                width={100}
                widthUnit="%"
            >
                <Paragraph big style={{color: color.blue1, fontWeight: "bold"}}
                           mixed={true}>
                    <FlexLink to="/">
                        <IconCircle circleColor="transparent" iconColor={color.blue1}>
                            <ArrowSVG/>
                        </IconCircle>
                        Series LLC
                    </FlexLink>
                </Paragraph>

                <Paragraph big>
                    This is a special type of LLC entity that's only available in certain states. It allows you
                    to create "mini" LLCs, each with their own limited liability and separate assets, under the
                    umbrella of a master LLC.
                </Paragraph>
            </TextBlockWithImage>

            <TextBlockWithImage
                SvgImage={PlaneIcon}
                textBackgroundColor="transparent"
                imageBackgroundColor={color.white}
                imageShadowColor={shadow.white2}
                imageShadowOpacity={0.5}
                boxShadow={false}
                paddingLeft={0}
                style={{marginTop: "40px"}}
                circleShadowY={40}
                circleShadowBlure={80}
                width={100}
                widthUnit="%"
            >
                <Paragraph big style={{color: color.blue1, fontWeight: "bold"}}
                           mixed={true}>
                    <FlexLink to="/">
                        <IconCircle circleColor="transparent" iconColor={color.blue1}>
                            <ArrowSVG/>
                        </IconCircle>
                        S Corporation
                    </FlexLink>
                </Paragraph>

                <Paragraph big>
                    This is a more complex type of business and isn't generally recommended for smaller
                    organization.
                </Paragraph>
            </TextBlockWithImage>

            <TextBlockWithImage
                SvgImage={RockerIcon}
                textBackgroundColor="transparent"
                imageBackgroundColor={color.white}
                imageShadowColor={shadow.white2}
                imageShadowOpacity={0.5}
                boxShadow={false}
                paddingLeft={0}
                style={{marginTop: "40px"}}
                circleShadowY={40}
                circleShadowBlure={80}
                width={100}
                widthUnit="%"
            >
                <Paragraph big style={{color: color.blue1, fontWeight: "bold"}}
                           mixed={true}>
                    <FlexLink to="/">
                        <IconCircle circleColor="transparent" iconColor={color.blue1}>
                            <ArrowSVG/>
                        </IconCircle>
                        C Corporation
                    </FlexLink>
                </Paragraph>

                <Paragraph big>
                    These are the largest and most complex types of businesses and are far more than the average
                    entrepreneur or business owner will need.
                </Paragraph>
            </TextBlockWithImage>

            <Paragraph big style={{paddingTop: "48px", paddingBottom: 0}} mixed={true}>
                For most smaller consultant businesses, an LLC is the ideal business setup. It provides you with
                liability protection, is simple to administer and has some tax advantages over other business
                types. Additionally, if another consulting firm ever subcontracts to you (which does happen),
                they will definitely expect you to have a formal legal structure in place.
            </Paragraph>
            <Paragraph big style={{paddingTop: "48px", paddingBottom: 0}} mixed={true}>
                For more information on the advantages and disadvantages of different types of businesses, <Link
                to="/">please see our in-depth guide</Link>. If youve still got questions, <Link to="/">weve
                answered them to help you choose the right business structure.</Link>
            </Paragraph>
            <Paragraph big style={{paddingTop: "48px", paddingBottom: 0}} mixed={true}>
                Weve <Link to="/">got a complete guide to everything you need to do to set up an LLC</Link>, and we
                can start one
                for your consulting business today. <Link to="/">LLC formation does vary from state to state</Link>,
                but weve got
                you covered, wherever you are.
            </Paragraph>

            <Button theme="primary48" width="365px" arrow>
                Start your business with us, today!
            </Button>
        </ImageContent>
    </BusinessOwnerResponsibilities>

</OrangeContainer>;

const FlexLink = styled(Link)`
    display: flex;
    align-items: center;
    justify-content: flex-start;
`;

export default BusinessStructure;
